<template>
  <div>
    <el-tabs v-model="activeName" @tab-click="handleClick" :stretch="true" height="100%">
      <el-tab-pane :label="item" :name="item" :key="index" v-for="(item,index) in paneList">
        <div class="mw-person-board" v-for="(personItem,personIndex) in list[item]" :key="personIndex">
          <div class="mw-avatar">
            <el-image
                style="width: 100%;height: 100%"
                :src="`/${personItem.pic_h}vertical.jpg`"
                fit="contain" lazy></el-image>
          </div>
          <ul class="mw-content">
            <li class="mw-person-name">{{ personItem.name }}</li>
            <li class="mw-person-mote" v-html="personItem.mote"></li>
            <li class="mw-person-teacher">师承：{{ personItem.teacher }}</li>
          </ul>
        </div>
      </el-tab-pane>
    </el-tabs>
  </div>
</template>

<script>
export default {
  name: "Person",
  data () {
    return {
      // 当前激活标签页
      activeName: '',
      // 技术员数组
      paneList: [],
      list: {},
      err: ''
    }
  },
  methods: {
    // 标签页点击事件
    handleClick () {
      document.getElementsByClassName('el-tabs__content')[0].scrollTop=0
    }
  },
  created () {
    this.axios.get('../staffs.json').then(response => {
    // this.axios.get('json/staffs.json').then(response => {
      // this.axios.get('https://tmp.fyscu.com/staffs.json').then(response => {
      this.list = response.data
      Object.keys(response.data).forEach(ele => {
        this.paneList.push(ele)
      })
      this.paneList.sort((a, b) => b - a)
      if (this.paneList.length) {
        this.activeName = this.paneList[0]
      }
    }).catch(function (error) {
      console.log(error)
    })
  }
}
</script>

